export default {
	data() {
		return {
			paramsArr: [
				{
					name: '当前区县',
					key: 'field06'
				},
				{
					name: '目前健康状态',
					key: 'field07'
				},
				{
					name: '今日体温',
					key: 'field08'
				},
				{
					name: '自2020年1月15日起是否途径湖北省',
					key: 'field09'
				},
				{
					name: '自2020年1月15日起是否接触过来自重点疫区人员或者被隔离人员',
					key: 'field10'
				},
				{
					name: '自2020年1月15日起是否接触过疑似或确诊的新型冠状病毒肺炎患者',
					key: 'field11'
				},
				{
					name: '是否乘坐长途公交',
					key: 'field12'
				}
			],
			dataArr: [
				// {
				// 	title: '2020-03-09',
				// 	field06: '10',
				// 	field07: '20',
				// 	field08: '15',
				// 	field09: '25',
				// 	field10: '4',
				// 	field11: '011',
				// 	field12: '25',
				// 	field13: '2',
				// 	field14: '',
				// 	isActive: true
				// }
			],
			vehicleTranslate:{
				'飞机':['飞机-航班号','飞机-起降地点','飞机-座位号'],
				'火车':['火车-车次','火车-乘车区间','火车-车厢及座位号'],
				'汽车':['汽车-起止地点','汽车-是否司机'],
				'轮船':['轮船-起止地点']
			}
		};
	},
	mounted(){
		this.getData()
	},
	methods: {
		getData(){
			this.$HTTP({
				url:'/healthForDay/list',
				showLoading:false,
				params:{
					"field01":uni.getStorageSync('userId'),
					"pageNum":"",
					"pageSize":"",
				} ,
				successCallback:({data})=>{
					if(data.code == '0'){
						let arr = data.data.list.map(item=>{
							return{
								...item,
								title:new Date(item.addTime).Format('yyyy-MM-dd')
							}
						})
						this.dataArr = arr
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
				}
				
			})
		},
		handleCollapse(item, index) {
			this.$set(item, 'isActive', !item.isActive);
		}
	}
};
